import React from 'react';
import { Component } from 'react';

import * as THREE from 'three/build/three.module';

import { TWEEN } from 'three/examples/jsm/libs/tween.module.min';
import { TrackballControls } from '../js/TrackballControls.js';
import { CSS3DRenderer, CSS3DObject } from '../js/CSS3DRenderer.js';

class TechSphere extends Component {

    componentDidMount() {
        this.initializeSphere();
    }

    render() {
        return (
            <React.Fragment>
                <div id="container"></div>
                <div id="menu" className="display-none">
                    <button id="table">TABLE</button>
                    <button id="sphere">SPHERE</button>
                    <button id="helix">HELIX</button>
                    <button id="grid">GRID</button>
                </div>
            </React.Fragment>
        )
    }

    initializeSphere = () => {

        window.transformInProgress = false;

        var table = [
            "AJAX", "", "Async JS", 1, 1,
            "Prototyping", "", "Wireframing", 18, 1,
            "jQuery", "", "JavaScript Library", 1, 2,
            "Scrum", "", "Workplace", 2, 2,
            "Node.js", "", "Back-end Programming", 13, 2,
            "PHP", "", "Back-end Programming", 14, 2,
            "Git", "", "Version COntrol", 15, 2,
            "JSON", "", "Data Manipulation", 16, 2,
            "MySQL", "", "Database Management", 17, 2,
            "HTML", "", "HTML5", 18, 2,
            "CSS", "", "CSS3", 1, 3,
            "Invision", "", "Prototyping", 2, 3,
            "React", "", "ReactJS JavaScript Library", 13, 3,
            "JavaScript", "", "My Core Language", 14, 3,
            "REST API", "", "API Development", 15, 3,
            "Redis", "", "Database Management", 16, 3,
            "MVC", "", "Framework", 17, 3,
            "WebSocket", "", "No loading necessary", 18, 3,
            "Agile", "", "Workplace", 1, 4,
            "Code Igniter", "", "PHP Framework", 2, 4,
            "Kanban", "", "Workplace", 3, 4,
            "UI/UX", "", "Business Side", 4, 4
        ];

        var camera, scene, renderer;
        var controls;

        var objects = [];
        var targets = { table: [], sphere: [], helix: [], grid: [] };

        var camera_angle = 1.57; // 1.57 basically when plugged in into the equation for the camera position results in the correct default position of the sphere
        var camera_rotation_interval;
        var camera_rotation_run = true; // this condition is used for the automatic rotation of the sphere. such that when it is set into false, auto-rotate will not occur

        init();
        animate();

        function init() {

            camera = new THREE.PerspectiveCamera(40, window.innerWidth / window.innerHeight, 1, 10000);
            camera.position.z = 3000;

            scene = new THREE.Scene();

            // table

            for (var i = 0; i < table.length; i += 5) {

                var element = document.createElement('div');
                element.className = 'element';
                element.style.backgroundColor = 'rgba(0,127,127,' + (Math.random() * 0.5 + 0.25) + ')';

                var number = document.createElement('div');
                number.className = 'number';
                number.textContent = (i / 5) + 1;
                element.appendChild(number);

                var symbol = document.createElement('div');
                symbol.className = 'symbol';
                symbol.textContent = table[i];
                element.appendChild(symbol);

                var details = document.createElement('div');
                details.className = 'details';
                details.innerHTML = table[i + 1] + '<br>' + table[i + 2];
                element.appendChild(details);

                // initial random position
                var object = new CSS3DObject(element);
                var x_pos = Math.random() * 4000 - 2000;
                var y_pos = Math.random() * 4000 - 2000;
                var z_pos = Math.random() * 4000 - 2000;
                object.position.x = x_pos;
                object.position.y = y_pos;
                object.position.z = z_pos;
                scene.add(object);
                objects.push(object);

                // table positioning
                object = new THREE.Object3D();
                // object.position.x = (table[i + 3] * 140) - 1330;
                // object.position.y = - (table[i + 4] * 180) + 990;
                object.position.x = x_pos;
                object.position.y = y_pos;
                object.position.z = z_pos;
                targets.table.push(object);

            }

            // sphere

            var vector = new THREE.Vector3();

            for (var i = 0, l = objects.length; i < l; i++) {

                var phi = Math.acos(- 1 + (2 * i) / l);
                var theta = Math.sqrt(l * Math.PI) * phi;

                var object = new THREE.Object3D();

                object.position.setFromSphericalCoords(600, phi, theta);

                vector.copy(object.position).multiplyScalar(2);

                object.lookAt(vector);

                targets.sphere.push(object);

            }

            // helix

            var vector = new THREE.Vector3();

            for (var i = 0, l = objects.length; i < l; i++) {

                var theta = i * 0.175 + Math.PI;
                var y = - (i * 8) + 450;

                var object = new THREE.Object3D();

                object.position.setFromCylindricalCoords(900, theta, y);

                vector.x = object.position.x * 2;
                vector.y = object.position.y;
                vector.z = object.position.z * 2;

                object.lookAt(vector);

                targets.helix.push(object);

            }

            // grid

            for (var i = 0; i < objects.length; i++) {

                var object = new THREE.Object3D();

                object.position.x = ((i % 5) * 400) - 800;
                object.position.y = (- (Math.floor(i / 5) % 5) * 400) + 800;
                object.position.z = (Math.floor(i / 25)) * 1000 - 2000;

                targets.grid.push(object);

            }

            //

            renderer = new CSS3DRenderer();
            renderer.setSize(window.innerWidth, window.innerHeight);
            document.getElementById('container').appendChild(renderer.domElement);
            document.getElementById('container').addEventListener('click', function () {
                try {
                    clearInterval(camera_rotation_interval);
                    camera_rotation_run = false;
                } catch (e) {
                    console.log(`camera_rotation_interval not init`)
                }
            })

            //

            controls = new TrackballControls(camera, renderer.domElement);
            controls.minDistance = 500;
            controls.maxDistance = 6000;
            controls.noZoom = true;
            controls.addEventListener('change', render);

            var button = document.getElementById('table');
            button.addEventListener('click', function () {

                transform(targets.table, 2000);

            }, false);

            var button = document.getElementById('sphere');
            button.addEventListener('click', function () {

                transform(targets.sphere, 2000);

            }, false);

            var button = document.getElementById('helix');
            button.addEventListener('click', function () {

                transform(targets.helix, 2000);

            }, false);

            var button = document.getElementById('grid');
            button.addEventListener('click', function () {

                transform(targets.grid, 2000);

            }, false);

            transform(targets.table, 1000);

            //

            window.addEventListener('resize', onWindowResize, false);
            window.addEventListener('scroll', onWindowScroll, false);

        }

        function transform(targets, duration) {

            TWEEN.removeAll();

            for (var i = 0; i < objects.length; i++) {

                var object = objects[i];
                var target = targets[i];

                new TWEEN.Tween(object.position)
                    .to({ x: target.position.x, y: target.position.y, z: target.position.z }, Math.random() * duration + duration)
                    .easing(TWEEN.Easing.Exponential.InOut)
                    .start();

                new TWEEN.Tween(object.rotation)
                    .to({ x: target.rotation.x, y: target.rotation.y, z: target.rotation.z }, Math.random() * duration + duration)
                    .easing(TWEEN.Easing.Exponential.InOut)
                    .start();

            }

            new TWEEN.Tween(this)
                .to({}, duration * 2)
                .onUpdate(render)
                .start();

        }

        function onWindowResize() {

            camera.aspect = window.innerWidth / window.innerHeight;
            camera.updateProjectionMatrix();

            renderer.setSize(window.innerWidth, window.innerHeight);

            render();

        }

        function onWindowScroll() {

            let skillsSection = document.getElementById("container");

            if (window.transformInProgress === false) {

                if ((skillsSection.getBoundingClientRect().bottom - window.innerHeight) - (skillsSection.clientHeight / 2) <= 0) {
                    window.transformInProgress = true;
                    transform(targets.sphere, 1000);

                    try {
                        clearInterval(camera_rotation_interval)
                    } catch (e) {
                        console.log(`camera_rotation_interval not init`);
                    }

                    if (camera_rotation_run === true) {

                        setTimeout(() => {
                            camera_rotation_interval = setInterval(() => {
                                var radius = 3000;
                                camera.position.x = radius * Math.cos(camera_angle);
                                camera.position.z = radius * Math.sin(camera_angle);
                                camera_angle += 0.003;
                            }, 10)
                        }, 1000)

                    }

                    setTimeout(() => {
                        window.transformInProgress = false;
                    }, 2000)
                }
            }

        }

        function animate() {

            requestAnimationFrame(animate);

            TWEEN.update();

            controls.update();

        }

        function render() {

            renderer.render(scene, camera);

        }
    }

}

export default TechSphere;