import React from 'react';
import { Component } from 'react';
import GlobalLoader from './GlobalLoader';
import TechSphere from './TechSphere';
import BackToTop from './BackToTop';

// import aboutMeVideo from '../media/portfolio.mp4';
import tzillaImg from '../media/tzilla-port.jpg';
import jwsImg from '../media/jwservice-port.jpg';
import esportsImg from '../media/esports-port.jpg';

class Home extends Component {

    state = {

        //states for main heading animation
        h1AnimationInProgress: false,
        h2AnimationInProgress: false,
        selectionAnimationInProgress: false,
        aboutMeVideoIsActive: false,

        // states for scroll elements animation. scroll elements are basically the elements that would have flipInX animation when they are visible when scrolled to
        scrollElementIsVisible: [],

        globalLoaderIsVisible: true,
        backToTopIsVisible: false,

        windowScrollY: 0,
        jgMomentumScrollElementHeight: 0
    }

    componentDidMount() {
        window.addEventListener("load", this.startHeadingAnimations);
        window.addEventListener("scroll", this.handleWindowScrollAndResize);
        window.addEventListener("resize", this.handleWindowScrollAndResize);
    }

    componentWillUnmount() {
        window.removeEventListener("load", this.startHeadingAnimations);
        window.removeEventListener("scroll", this.handleWindowScrollAndResize);
        window.addEventListener("resize", this.handleWindowScrollAndResize);
    }

    render() {

        return (

            <article>

                <GlobalLoader
                    globalLoader={this.state.globalLoaderIsVisible}
                />

                <div className="about-me-video-container">
                    <div className="about-me-video-opacitizer"></div>
                    <video id="about-me-video" className={this.state.aboutMeVideoIsActive ? 'active' : ''} ref={(ref) => this.aboutMeVideo = ref} muted>
                        <source src={`https://drive.google.com/uc?export=download&id=1xjwAbVuewHgbZBeOX3DiHFXogmXtbFKc`}></source>
                        {/* <source src={aboutMeVideo}></source> */}
                    </video>
                </div>

                <div className="jg-momentum-scroll-container">
                    <main className="jg-main jg-momentum-element" ref={(ref) => this.jgMomentumScrollElement = ref} style={{ transform: `translateY(-${this.state.windowScrollY}px)` }}>

                        <section className="main-heading">
                            <div className={`selection`}>
                                <h1 className={`jg-main-h1 ${this.state.h1AnimationInProgress ? 'flipInX animated slow' : ''}`}>JSON-G</h1>
                                <h2 className={`jg-main-h2 ${this.state.h2AnimationInProgress ? 'flipInX animated slow' : ''}`}>Expert Web Developer</h2>
                                <span className={`selection-item ${this.state.selectionAnimationInProgress ? 'flipInX animated slow' : ''}`} onClick={this.handleScroll(this.sectionAboutMe)}>About me</span>
                                {/* <Link className={`selection-item ${this.state.selectionAnimationInProgress ? 'flipInX animated slow' : ''}`} to="/testroute">Test Route</Link> */}
                            </div>
                            {/* <div className="about-me-video-container">
                                <div className="about-me-video-opacitizer"></div>
                                <video id="about-me-video" className={this.state.aboutMeVideoIsActive ? 'active' : ''} ref={(ref) => this.aboutMeVideo = ref} muted>
                                    <source src={`https://drive.google.com/uc?export=download&id=1xjwAbVuewHgbZBeOX3DiHFXogmXtbFKc`}></source>
                                </video>
                            </div> */}
                        </section>

                        <section className="about-me" ref={(ref) => this.sectionAboutMe = ref}>
                            <div className="about-me-inner">
                                <p>I am Jason Gonzales (JSON-G).</p>
                                <p>I like building websites.</p>
                                <br></br>
                                <p>I specialize in Software Engineering in an Agile Environment.</p>
                            </div>
                        </section>

                        <section className="skills">
                            <TechSphere></TechSphere>
                        </section>

                        <section className="my-work">
                            <div className="sect-inner-container">
                                {this.generateHTMLForMyWork()}
                            </div>
                        </section>

                        <section className="contact-me">
                            <span>Let's build something great!<br></br>My email: </span>
                            <a href="mailto:gjasonaaron@gmail.com">gjasonaaron@gmail.com</a>
                            <div className="social-media">
                                <div>
                                    <a href="https://stackoverflow.com/users/8680805/95faf8e76605e973" target="_blank" rel="noopener noreferrer">Stack Overflow</a>
                                </div>
                                <div>
                                    <a href="https://github.com/JSON-G" target="_blank" rel="noopener noreferrer">Github</a></div>
                            </div>
                        </section>

                    </main>
                </div>

                <div className="jg-momentum-scroll-hitbox" style={{ height: `${this.state.jgMomentumScrollElementHeight}px` }}></div>

                <BackToTop
                    handleScroll={this.handleScroll}
                    backToTopIsVisible={this.state.backToTopIsVisible}
                />

            </article>
        )
    }

    generateHTMLForMyWork = () => {

        this.scrollElement = [];

        return (

            <React.Fragment>

                <div className={`my-work-item flipInXInitial ${this.state.scrollElementIsVisible[0] ? 'flipInX animated slow' : ''}`} ref={(ref) => this.scrollElement[0] = ref}>
                    <div className="my-work-item-image-container">
                        <img src={tzillaImg} alt="Tzilla Website"></img>
                    </div>
                    <div className="my-work-item-desc">
                        <div>Tzilla</div>
                        <div className="my-work-item-inner-desc">I currently work here as a Software Engineer. I provide solutions in UI/UX and front-end development.</div>
                        <a className="my-work-item-inner-link" href="https://www.tzilla.com" target="_blank" rel="noopener noreferrer">Visit Website</a>
                    </div>
                    <div>
                    </div>
                </div>

                <div className={`my-work-item flipInXInitial ${this.state.scrollElementIsVisible[1] ? 'flipInX animated slow' : ''}`} ref={(ref) => this.scrollElement[1] = ref}>
                    <div className="my-work-item-image-container">
                        <img src={jwsImg} alt="JWS Website"></img>
                    </div>
                    <div className="my-work-item-desc">
                        <div>JWS</div>
                        <div className="my-work-item-inner-desc">I own this website. JWS (Json Web Service or jwservice) has Chat Rooms which can be used for sending text messages in real time. JWS has special tools for sharing source code. JWS has Ephemeral Cloud Storage where you can share your files temporarily.</div>
                        <a className="my-work-item-inner-link" href="https://jwservice.herokuapp.com" target="_blank" rel="noopener noreferrer">Visit Website</a>
                    </div>
                    <div>
                    </div>
                </div>

                <div className={`my-work-item flipInXInitial ${this.state.scrollElementIsVisible[2] ? 'flipInX animated slow' : ''}`} ref={(ref) => this.scrollElement[2] = ref}>
                    <div className="my-work-item-image-container">
                        <img src={esportsImg} alt="E-sports Website"></img>
                    </div>
                    <div className="my-work-item-desc">
                        <div>E-sports.cool</div>
                        <div className="my-work-item-inner-desc">I worked here as a home-based full stack developer. I developed Steam Powered Digital Asset Trading, e-sports tournament platform, social platform, and NEM mosaic blockchain powered COOL tokens trading.</div>
                    </div>
                    <div>
                    </div>
                </div>

            </React.Fragment>
        );
    }

    startHeadingAnimations = () => {

        let delay = 1000; // milliseconds

        // remove loader since website is now fully loaded
        setTimeout(() => {
            this.setState({
                globalLoaderIsVisible: false
            }, function () {
                window.scrollTo(0, 0)
            });

            this.startH1Animation();
            this.startH2Animation();
            this.startVideoAnimation();
            this.startSelectionAnimation();
        }, delay)

    }

    startH1Animation = () => {
        this.setState({
            h1AnimationInProgress: true
        });
    }

    startH2Animation = () => {
        let h1_animation_duration = 1000; // milliseconds
        setTimeout(
            () => {
                this.setState({
                    h2AnimationInProgress: true
                });
            },
            h1_animation_duration
        );
    }

    startVideoAnimation = () => {
        let h1_plus_h2_animation_duration = 2000; // milliseconds
        setTimeout(
            () => {
                this.playAboutMeVideo();
                setTimeout(() => {
                    document.body.setAttribute("class", "")
                    this.setState({
                        jgMomentumScrollElementHeight: this.jgMomentumScrollElement.offsetHeight
                    });
                }, 1500);
            },
            h1_plus_h2_animation_duration
        );
    }

    startSelectionAnimation = () => {
        let h1_plus_h2_animation_duration = 2000; // milliseconds
        setTimeout(
            () => {
                this.setState({
                    selectionAnimationInProgress: true
                });
            },
            h1_plus_h2_animation_duration
        );
    }

    stopHeadingAnimations = () => {
        this.setState({
            h1AnimationInProgress: false,
            h2AnimationInProgress: false,
            aboutMeVideoIsActive: false,
            selectionAnimationInProgress: false
        });
    }

    playAboutMeVideo = () => {
        this.setState({
            aboutMeVideoIsActive: true
        });
        this.aboutMeVideo.currentTime = 0;
        this.aboutMeVideo.play();
    }

    /**
     * Scroll to element smoothly
     * @param {HTMLElement} element - HTMLElement to scroll into (smoothly)
     * @returns {function(Event): void} a function accepting any event which takes place in the DOM but is optional and has default value of "override" so it can be executed without an event
     */
    handleScroll = (element) => (e = "override") => {

        // element position is defaulted to 0
        // this enables "back to top" feature
        // to use this feature: simply pass undefined to this function
        var element_position = 0;

        if (element !== undefined) {
            //get element's position (topmost) relative to document
            var rect = element.getBoundingClientRect();
            var scrollTop = window.pageYOffset || 0;
            element_position = rect.top + scrollTop;
        }

        // current position of the scrollbar (y-axis)
        var current_position = window.scrollY || window.screenTop;

        // setInterval procs (in milliseconds)
        var time = 10;

        // (number) position increment for scrolling
        var i = current_position;

        // interval ID (needed for clearing the interval function)
        var x;

        if (current_position < element_position) {
            x = setInterval(function () {
                window.scrollTo(0, i);
                i += 30;
                if (i >= element_position) {
                    clearInterval(x);
                }
            }, time);
        } else {
            x = setInterval(function () {
                window.scrollTo(0, i);
                i -= 30;
                if (i <= element_position) {
                    clearInterval(x);
                }
            }, time);
        }
    }

    cloneObjUsingJson = (obj) => {
        return JSON.parse(JSON.stringify(obj));
    }

    handleWindowScrollAndResize = e => {

        this.setState({
            windowScrollY: window.scrollY,
            jgMomentumScrollElementHeight: this.jgMomentumScrollElement.offsetHeight
        })

        let workRefsLength = this.scrollElement.length;

        if (workRefsLength > 0) {

            let _scrollElementIsVisible = this.cloneObjUsingJson(this.state.scrollElementIsVisible);
            let thereAreChanges = false;

            for (var i = 0; i < workRefsLength; i++) {

                if ((this.scrollElement[i].getBoundingClientRect().bottom - window.innerHeight) - (this.scrollElement[i].clientHeight / 4) <= 0) {
                    _scrollElementIsVisible[i] = true;
                    thereAreChanges = true;
                }
            }

            if (thereAreChanges === true) {
                this.setState({
                    scrollElementIsVisible: _scrollElementIsVisible
                });
            }
        }

        // detects if back-to-top element should be visible
        if (window.scrollY > 300) {
            if (this.state.backToTopIsVisible === false) {
                this.setState({
                    backToTopIsVisible: true
                });
            }
        } else {
            if (this.state.backToTopIsVisible === true) {
                this.setState({
                    backToTopIsVisible: false
                });
            }
        }
    }
}

export default Home;