import React, { Component } from 'react';

class GlobalLoader extends Component {

    render() {
        return (
            <div className={`global-loader ${this.props.globalLoader ? "ajax-in-progress" : ""}`}>
                <div className="global-loader-loading-elem">
                    <small className={`vert-bars-loading`}>
                        <div className={`vert-bars-item `}></div>
                        <div className={`vert-bars-item `}></div>
                        <div className={`vert-bars-item `}></div>
                        <div className={`vert-bars-txt `}>{this.props.globalLoaderText || "Loading..."}</div>
                    </small>
                </div>
            </div>
        );
    }
}

export default GlobalLoader;